.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 12.96px;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
}

.card__product-image {
  width: 160px;
  margin: 0 auto;
  padding-top: 16px;
}

.card__product-image img {
  vertical-align: bottom;
  width: 160px;
  height: 134px;
}

.card__product-name {
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
}

.card__product-code {
  color: #616070;
  padding-top: 4px;
  font-size: 10.08px;
  line-height: 14.04px;
}

.card__reviews {
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.card__number-of-reviews {
  align-self: flex-end;
  font-size: 10.08px;
  line-height: 14.04px;
}

.card__product-price {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.card__price-tittle {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-weight: 700;
}

.card__buy-link {
  padding-top: 16px;
}

.card__buy-link a {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14.08px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy-link a:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .4s;
}

body {
  box-sizing: border-box;
  color: #060b35;
